import { Component } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ProvideExpensesService } from './provide-expense-service/provide-expenses.service';
import { ProvideExpensesModalComponent } from './provide-expenses-modal/provide-expenses-modal/provide-expenses-modal.component';
import { ExpenseRequestDetailsComponent } from './expense-request-details/expense-request-details.component';
import { TableColumn, TableConfig } from '../../../../shared/components/table-advanced/table-advanced-interfaces';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime, distinctUntilChanged } from 'rxjs';
import { HttpserviceService } from '../../../../services/httpservice.service';
import { CommonService } from '../../../../services/common.service';
import { PettyCashService } from '../../cashier/petty-cash/services/petty-cash.service';
import { ModalMessageComponent } from '../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'app-provide-expenses',
  templateUrl: './provide-expenses.component.html',
  styleUrls: ['./provide-expenses.component.scss'],
})
export class ProvideExpensesComponent {
  //searchText: string = '';
  //sort: number = 1;
  //totalRecords: number = 18;
  //pageNo: number = 1;
  //pagin: number;
  //pages: any[] = [];
  //provideExpenses: any;
  //search: string;
  provideExpensesList = [];
  pageNo = 1;
  total = 0;
  limit = 6;
  tableConfig: TableConfig = {
    paging: true,
    filter: {
      Sort: 1,
      PageSize: this.limit,
    },
  };
  tableColumns: TableColumn[] = [];
  filterForm: FormGroup;
  filterParams = '';
  uploadedFile;
  constructor(
    private modalService: NgbModal,
    private fb: FormBuilder,
    private _commonService: CommonService,
    private _provideExpensesService: ProvideExpensesService,
    private _pettyCashService: PettyCashService,
  ) { }

  ngOnInit(): void {
    this.initTableColumns();
    this.initFilterForm();
    this.getList();
  }
  get f() {
    return this.filterForm;
  }
  initFilterForm() {
    this.filterForm = this.fb.group({
      replenishmentRequest: [''],
      receipt: [''],
      appDate: [''],
      requestApprovalDateFrom: [''],
      requestApprovalDateTo: [''],
      proDate: [''],
      providingDateFrom: [''],
      providingDateTo: [''],
    });
    this.filterForm.valueChanges.pipe(debounceTime(500), distinctUntilChanged()).subscribe((data) => {
      this.pageNo = 1;
      let formValues = this._commonService.trim(data);
      if (formValues?.appDate) {
        formValues = {
          ...formValues,
          requestApprovalDateFrom: this._commonService.transformDate(formValues?.appDate[0]),
          requestApprovalDateTo: this._commonService.transformDate(formValues?.appDate[1])
        };
      }
      if (formValues?.proDate) {
        formValues = {
          ...formValues,
          providingDateFrom: this._commonService.transformDate(formValues?.proDate[0]),
          providingDateTo: this._commonService.transformDate(formValues?.proDate![1])
        };
      }
      delete formValues.appDate;
      delete formValues.proDate;
      this.filterParams = new URLSearchParams(formValues).toString();
      this.getList(this.filterParams);
    });
  }

  onFileChange(event, row) {
    let files = [...event.target.files];
    if (files.length > 0) {
      let isInvalid = this._commonService.checkInvalidImageFormat(files);
      if (!isInvalid) {
        files.forEach((file: File) => {
          this._commonService.fileToBase64(file).then(response => {
            this.uploadedFile = response;
            this.uploadConfirmReceivePettyCashAmount(row)
          })
        })
      }
    }
  }
  uploadConfirmReceivePettyCashAmount(row) {
    if (!this.uploadedFile?.file) {
      return;
    }
    let formData = new FormData();
    formData.append('PettyCashRequestId', row?.pettyCashRequestId);
    formData.append('ConfirnReceivingVoucher', this.uploadedFile.file);
    this._pettyCashService.uploadConfirmReceivePettyCashAmount(formData).subscribe(response => {
      console.log(response);
      if (response.data) {
        this.responseModal('success', 'The receipt has been uploaded successfully');
      }
    }, err => {
      this.responseModal('error', err?.error?.errors);

    })
  }
  responseModal(type, message) {
    const ref = this.modalService.open(ModalMessageComponent);
    ref.componentInstance.type = type;
    ref.componentInstance.dismissOnlyActive = true;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }
  getList(params?) {
    let apiParams = '';
    let defaultParams = `pageSize=${this.tableConfig.filter.PageSize}&pageNo=${this.pageNo - 1}&sort=${this.tableConfig.filter.Sort}`;
    if (params) apiParams = `?${params}&${defaultParams}`;
    else apiParams = `?${defaultParams}`;
    this._provideExpensesService.getProvideExpenses(apiParams)
      .subscribe((response) => {
        if (response.data.length) {
          this.provideExpensesList = response?.data;
          this.total = response?.totalRecordCount;
        }
      });
  }
  onSortChange(sort: any) {
    if (sort?.direction && sort?.column) {
      switch (sort.column) {
        case 'register.branch.branchName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 3 : 2;
          break;
        case 'register.registerId':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 5 : 4;
          break;
        case 'modifiedAt':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 7 : 6;
          break;
        case 'createdBy.fullName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 9 : 8;
          break;
        case 'approvedAmount':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 11 : 10;
          break;
        default:
          break;
      }
    } else {
      this.tableConfig.filter.Sort = 1;
    }
    this.getList();
  }
  onPageChange(page: number) {
    this.pageNo = page;
    this.getList();
  }
  initTableColumns() {
    this.tableColumns = [
      { key: 'pettyCashRequestId', label: 'Request No.' },
      { key: 'register.branch.branchName', label: 'Branch', canSort: true, },
      { key: 'register.registerId', label: 'Register No', canSort: true },
      { key: 'modifiedAt', label: 'Request Approval Date', canSort: true, dateFormat: 'mediumDate', placeholder: '---' },
      { key: 'providedAt', label: 'Providing Date', dateFormat: 'mediumDate', placeholder: '---' },
      { key: 'createdBy.fullName', label: 'Cashier', canSort: true, },
      { key: 'approvedAmount', label: 'Amount', canSort: true, currency: { decimalFormat: '2.3-3', appendText: ' JOD' } },
      { key: 'action', label: 'Action', },
    ];
  }
  viewDetails(item) {
    const modalRef = this.modalService.open(ExpenseRequestDetailsComponent, { backdrop: 'static', keyboard: false, size: 'xl', });
    modalRef.componentInstance.data = { ...item, from: 'providePettyCash' };
  }
  openModalTrigger(item) {
    const modalRef = this.modalService.open(ProvideExpensesModalComponent, {
      backdrop: 'static',
      keyboard: false,
      size: 'xl',
    });
    modalRef.componentInstance.registerDetails = item;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      console.log('resendtoLoadDatasult', result);
      this.modalService.dismissAll();
      this.getList();
    });
  }
}
